.search-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 20px;
  padding: 12px;
  font-family: 'Courier New', Courier, monospace;
}

.search-title {
    text-align: center;
}

/* CARD */
.search-card {
  display: flex;
  flex-direction: column;
  font-family: "Times New Roman", serif;
}

/* IMAGE WRAPPER  THIS IS THE KEY */
.image-box {
  width: 100%;
  height: 360px;          /*  controls card height */
  overflow: hidden;
  border-radius: 5px;
  background: #ffffff;
}

/* IMAGE ITSELF */
.image-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;     /*  magic line */
  display: block;
}

/* TITLE */
.product-title {
  margin-top: 2px;
  font-size: 16px;
  font-weight: 500;
}

/* PRICE */
.price {
  font-size: 15px;
}
