.order-history-page {
  background: #ffffff;
  padding: 50px 20px 80px;
}

.order-history-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.order-history-title {
  font-family: "Times New Roman", serif;
  font-size: 28px;
  letter-spacing: 1.8px;
  color: #8b0d2b;
  margin-bottom: 28px;
  text-align: center;
}

.order-history-empty {
  color: #777;
  font-size: 15px;
  text-align: center;
}

.order-history-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.order-history-card {
  border: 1px solid #eee;
  padding: 18px 20px;
}

.order-history-header {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  align-items: flex-start;
  margin-bottom: 14px;
}

.order-history-id {
  font-size: 14px;
  font-weight: 600;
}

.order-history-date {
  font-size: 12px;
  color: #666;
}

.order-history-total {
  font-size: 14px;
  font-weight: 700;
}

.order-history-items {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 14px;
}

.order-history-item {
  display: grid;
  grid-template-columns: 70px 1fr;
  gap: 14px;
  align-items: center;
}

.order-history-item img {
  width: 70px;
  height: 90px;
  object-fit: cover;
  display: block;
}

.order-item-title {
  font-size: 14px;
  margin-bottom: 4px;
}

.order-item-size {
  font-size: 12px;
  color: #666;
  margin-bottom: 4px;
}

.order-item-qty {
  font-size: 12px;
  color: #444;
}

.order-history-summary {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 13px;
  color: #333;
}

.order-summary-total {
  font-weight: 700;
}

@media (max-width: 600px) {
  .order-history-card {
    padding: 14px 16px;
  }

  .order-history-header {
    flex-direction: column;
    align-items: flex-start;
  }
}
