.profile-container {
  width: 100%;
  background: #ffffff;
  padding: 40px 0 80px;
}

.profile-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.profile-tabs {
  display: flex;
  gap: 28px;
  margin-bottom: 36px;
}

.profile-tabs button {
  background: none;
  border: none;
  font-family: "Times New Roman", serif;
  font-size: 14px;
  letter-spacing: 1.6px;
  cursor: pointer;
  padding-bottom: 6px;
  text-transform: uppercase;
  color: #333;
}

.profile-tabs button.active {
  border-bottom: 1px solid #333;
}

.address-title {
  text-align: center;
  font-family: "Times New Roman", serif;
  font-size: 26px;
  letter-spacing: 1.8px;
  color: #8b0d2b;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.add-address-btn {
  display: inline-block;
  margin: 0 auto 36px;
  padding: 10px 24px;
  border: 1px solid #333;
  background: transparent;
  cursor: pointer;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}

.address-section {
  text-align: center;
}

.address-table {
  text-align: left;
  margin: 0 auto;
  max-width: 1000px;
  border-top: 1px solid #ffffff;
}

.address-table-header {
  display: grid;
  grid-template-columns: 1fr 2.4fr 1.2fr;
  padding: 12px 16px;
  font-weight: 600;
  background: #f2f2f2;
  border: 1px solid #e2ded6;
  border-bottom: none;
}

.address-table-row {
  display: grid;
  grid-template-columns: 1fr 2.4fr 1.2fr;
  padding: 14px 16px;
  border-bottom: 1px solid #e2ded6;
  font-size: 14px;
}

.address-action {
  color: #8b0d2b;
  cursor: pointer;
}

.address-empty {
  padding: 18px 16px;
  color: #666;
  border-bottom: 1px solid #e2ded6;
}

.address-form {
  text-align: left;
  margin: 10px auto 0;
  max-width: 1000px;
}

.address-form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 22px 32px;
}

.address-field label {
  display: block;
  margin-bottom: 6px;
  font-size: 14px;
}

.required {
  color: #b00020;
}

.address-field input,
.address-field textarea,
.address-field select {
  width: 100%;
  padding: 14px 16px;
  background: #e9ebe6;
  border: none;
  outline: none;
  font-family: "Times New Roman", serif;
  font-size: 14px;
}

.address-field input:disabled {
  color: #777;
  cursor: not-allowed;
}

.address-field select:disabled {
  color: #777;
  cursor: not-allowed;
}

.address-field textarea {
  resize: none;
  min-height: 88px;
}

.select-wrapper {
  position: relative;
}

.select-trigger {
  width: 100%;
  padding: 14px 16px;
  background: #e9ebe6;
  border: none;
  outline: none;
  font-family: "Times New Roman", serif;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.select-wrapper.disabled .select-trigger {
  color: #777;
  cursor: not-allowed;
}

.select-placeholder {
  color: #777;
}

.select-caret {
  margin-left: 10px;
  font-size: 16px;
  transform: translateY(-1px);
}

.select-caret::before {
  content: "";
  display: inline-block;
  color: #555;
}

.select-panel {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  right: 0;
  background: #efefef;
  border: 1px solid #ddd;
  z-index: 10;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  padding: 10px;
}

.select-search {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #cfcfcf;
  background: #fff;
  font-family: "Times New Roman", serif;
  font-size: 14px;
  outline: none;
  margin-bottom: 10px;
}

.select-options {
  max-height: 220px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.select-option {
  text-align: left;
  background: transparent;
  border: none;
  padding: 8px 10px;
  cursor: pointer;
  font-family: "Times New Roman", serif;
  font-size: 14px;
  color: #444;
}

.select-option:hover,
.select-option.selected {
  background: #e1e1e1;
}

.select-empty {
  padding: 8px 10px;
  color: #888;
  font-size: 13px;
}

.field-error {
  display: block;
  margin-top: 6px;
  color: #b00020;
  font-size: 12px;
}

.address-form-actions {
  display: flex;
  gap: 18px;
  margin-top: 28px;
}

.submit-btn,
.cancel-btn {
  padding: 12px 30px;
  border: 1px solid #333;
  background: transparent;
  cursor: pointer;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  font-size: 13px;
}

.submit-btn:hover,
.cancel-btn:hover {
  background: #333;
  color: #fff;
}

@media (max-width: 900px) {
  .address-form-grid {
    grid-template-columns: 1fr;
  }

  .address-table {
    width: 100%;
  }
}

@media (max-width: 600px) {
  .profile-container {
    padding: 30px 0 60px;
  }

  .profile-tabs {
    gap: 18px;
  }

  .address-title {
    font-size: 22px;
  }

  .address-table-header,
  .address-table-row {
    grid-template-columns: 1fr;
    gap: 8px;
  }
}
