/* ================= TOP BAR ================= */

.top-bar {
  background: #8b0d2b;
  color: #fff;
  height: 48px;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  column-gap: 16px;
  position: relative;
  padding: 0 56px;
  z-index: 1000;
}

.top-left {
  position: static;
  font-size: 15px;
  white-space: nowrap;
  justify-self: start;
}

.top-text {
  font-size: 15px;
  white-space: nowrap;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  justify-self: center;
}

@media (min-width: 769px) {
  .top-bar {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .top-left {
    position: absolute;
    left: 56px;
  }

  .top-right {
    position: absolute;
    right: 56px;
  }

  .top-text {
    position: relative;
    left: auto;
    transform: none;
    justify-self: auto;
  }
}

.top-right {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-self: end;
}

.icon {
  font-size: 20px;
  cursor: pointer;
}

.icon-btn {
  background: none;
  border: none;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  line-height: 1;
  color: #fff;
  cursor: pointer;
}

.icon-btn:hover {
  opacity: 0.9;
}

.tooltip {
  position: relative;
}

.tooltip::after {
  content: attr(data-tooltip);
  position: absolute;
  left: 50%;
  bottom: -34px;
  transform: translateX(-50%);
  background: #111;
  color: #fff;
  font-size: 11px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  padding: 6px 10px;
  border-radius: 999px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease, transform 0.2s ease;
}

.tooltip::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #111;
  opacity: 0;
  transition: opacity 0.2s ease, transform 0.2s ease;
}

.tooltip:hover::after,
.tooltip:hover::before {
  opacity: 1;
  transform: translateX(-50%) translateY(-2px);
}

/* ================= LOGOUT MODAL ================= */
.logout-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3000;
  backdrop-filter: blur(2px);
}

.logout-modal {
  background: #fffaf6;
  border: 1px solid #eadfd6;
  padding: 22px 26px;
  width: min(420px, 90vw);
  text-align: center;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.18);
}

.logout-modal h3 {
  margin: 0 0 8px;
  font-family: "Times New Roman", serif;
  color: #8b0d2b;
  letter-spacing: 1px;
}

.logout-modal p {
  margin: 0 0 18px;
  font-size: 14px;
  color: #4b3f39;
}

.logout-actions {
  display: flex;
  justify-content: center;
  gap: 12px;
}

.logout-btn-primary,
.logout-btn-secondary {
  padding: 10px 18px;
  font-size: 13px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px solid #8b0d2b;
  background: transparent;
}

.logout-btn-primary {
  background: #8b0d2b;
  color: #fff;
}

.logout-btn-secondary:hover {
  background: #f3e6dd;
}

.logout-btn-primary:hover {
  background: #6f0a23;
}

.top-link {
  color: #fff;
  text-decoration: none;
  font-family: "Times New Roman", serif;
  font-size: 13px;
  letter-spacing: 1.2px;
}

.top-link:hover {
  text-decoration: underline;
}

.icon-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  line-height: 1;
}

.icon-link:hover {
  text-decoration: none;
  opacity: 0.9;
}

.logout-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}

@media (max-width: 900px) {
  .top-bar {
    padding: 6px 16px;
    height: auto;
    grid-template-columns: 1fr;
    row-gap: 6px;
  }

  .top-left,
  .top-text,
  .top-right {
    justify-self: center;
  }

  .top-left,
  .top-text {
    font-size: 13px;
  }
}

/* ================= SEARCH BAR ================= */

.search-bar {
  position: fixed;
  top: 40px;
  left: 0;
  width: 100%;
  background: #faf7f2;
  padding: 16px 20px;
  display: flex;
  gap: 12px;
  z-index: 2000;
  border-bottom: 1px solid #ddd;
}

.search-bar.hidden {
  opacity: 0;
  pointer-events: none;
}

.search-bar.active {
  opacity: 1;
  pointer-events: auto;
}

.search-bar input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  font-size: 16px;
}

.search-close {
  background: none;
  border: none;
  font-size: 22px;
  cursor: pointer;
}

/* ================= NAVBAR ================= */

.navbar {
  background: #ffffff;
  display: flex;
  align-items: center;
  padding: 10px 56px;
  position: relative;
  z-index: 999;
  padding-top: auto;
}

.logo img {
  height: 100px;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 30px;
  margin-left: auto;
    font-family: "Times New Roman", serif;
      color: #222;


}

/* NAV ITEMS */
.nav-links a,
.nav-links span,
.cart-link {
  font-family: "Times New Roman", serif;
  font-size: 13px;
  letter-spacing: 1.5px;
  color: #222;
  background: none;
  border: none;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  padding-bottom: 6px;
}

/* ================= HOVER UNDERLINE (RESTORED) ================= */

.nav-links a::after,
.nav-links span::after,
.cart-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #222;
  transition: width 0.3s ease;
}

.nav-links a:hover::after,
.nav-links span:hover::after,
.cart-link:hover::after {
  width: 100%;
}

/* ================= DROPDOWN (FIXED) ================= */

.dropdown {
  position: relative;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: #ffffff;
  padding: 20px 28px;
  display: none;
  z-index: 999;
  box-shadow: 0 8px 20px rgba(0,0,0,0.15);
  min-width: 240px;
  text-align: left;
}

/* show dropdown */
.dropdown:hover .dropdown-menu {
  display: block;
}

/* dropdown items  FIXED STACKING */
.dropdown-menu a {
  display: block;
  margin-bottom: 12px;
  font-family: "Times New Roman", serif;
  font-size: 15px;
  letter-spacing: 2px;
  line-height: 1.6;
  color: #555;
  text-decoration: none;
}

.dropdown-menu a:last-child {
  margin-bottom: 0;
}

/* ================= RIGHT ICONS ================= */

.nav-right {
  display: none;
  align-items: center;
  gap: 16px;
  margin-left: auto;
}

.cart-text {
  cursor: pointer;
}

.mobile-search {
  background: none;
  border: none;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  line-height: 1;
  cursor: pointer;
  color: #222;
}

.hamburger {
  background: none;
  border: none;
  font-size: 26px;
  cursor: pointer;
}

/* ================= MOBILE ================= */

@media (max-width: 768px) {
  .navbar {
    padding: 12px 16px;
    justify-content: space-between;
    gap: 12px;
    flex-wrap: nowrap;
  }

  .nav-links,
  .nav-links.desktop {
    display: none;
  }

  .nav-right {
    display: flex;
    margin-left: auto;
    flex: 0 0 auto;
  }

  .logo img {
    height: 48px;
  }
}

/* ================= MOBILE MENU ================= */
/* ================= MOBILE MENU (AASHAKA STYLE) ================= */
/* ================= MOBILE MENU (ANIMATED DROPDOWN) ================= */

.mobile-menu {
  background: #ffffff;
  padding: 0px 24px 24px;

  display: flex;
  flex-direction: column;
  gap: 10px;

  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transform: translateY(20px);

  transition: 
    max-height 0.55s ease,
    opacity 0.5s ease,
    transform 0.45s ease;
}

/* ================= MOBILE ACCORDION ================= */

.mobile-accordion {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.mobile-title {
  background: none;
  border: none;
  text-align: left;
  font-family: "Times New Roman", serif;
  font-size: 14px;
  letter-spacing: 2px;
  cursor: pointer;
  padding: 0 0 6px;
  line-height: 1.5;
  color: #494646;
  position: relative;
  text-transform: uppercase;
  display: inline-block;
  width: fit-content;

}

.mobile-title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #222;
  transition: width 0.3s ease;
}

.mobile-title:hover::after,
.mobile-title.active::after {
  width: 100%;
}

/* SUBMENU */
.mobile-submenu {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transform: translateY(2px);
  transition: all 0.55s ease;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding-left: 14px;


}

.mobile-submenu.open {
  max-height: 120px;
  opacity: 1;
  transform: translateY(1);
}

.mobile-submenu a {
  font-size: 13px;
  color: #666;
  text-decoration: none;
}


/* OPEN STATE */
.mobile-menu.open {
  max-height: 600px;
  opacity: 1;
  transform: translateY(0);
    margin-bottom: 10px;

}

/* Desktop safety */
@media (min-width: 769px) {
  .mobile-menu {
    display: none;
  }
}




/* Group wrapper */
.mobile-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Links */
.mobile-menu a {
  font-family: "Times New Roman", serif;
  font-size: 14px;
  letter-spacing: 1.8px;
  text-decoration: none;
  color: #444;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 6px;
  display: inline-block;
  width: fit-content;
}

.mobile-menu > a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #222;
  transition: width 0.3s ease;
}

.mobile-menu > a:hover::after {
  width: 42px;
}

/* Account links section */
.mobile-menu .account-link {
  color: #8b0d2b;
  letter-spacing: 1.4px;
}

.mobile-menu .mobile-logout {
  background: none;
  border: none;
  padding: 0;
  text-align: left;
  cursor: pointer;
}

/* Sub-links indentation */
.mobile-group a {
  padding-left: 12px;
  font-size: 13px;
  color: #666;
}

/* Desktop safety */
@media (min-width: 769px) {
  .mobile-menu {
    display: none;
  }
}


/* ================= MOBILE NAV SPACING FIX ================= */

@media (max-width: 480px) {

  /* Slightly reduce logo visual weight */
  .logo img {
    height: 48px;
  }

  /* Give navbar more breathing room */
  .navbar {
    padding: 12px 16px;
    align-items: center;

  }

  .top-text {
  font-size: 14px;
  white-space: nowrap;
  margin-right: 20px;
}

  /* Space between CART and hamburger */
  .nav-right {
    gap: 14px;
    align-items: center;

  }

  /* Make CART text less cramped */
  .cart-text {
    font-size: 12px;
    letter-spacing: 1.2px;
    align-items: center;

  }

  /* Ensure hamburger is easy to tap */
  .hamburger {
    padding: 1px;
    align-items: center;
  }
}

/* ================= MOBILE MATCH REFERENCE ================= */
@media (max-width: 768px) {
  .top-bar {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .top-left,
  .top-right {
    display: none;
  }

  .top-text {
    justify-self: center;
    text-align: center;
    white-space: normal;
    width: 100%;
  }
}
